/* Custom styles for the header */
.w3-sidebar a {
    font-family: "Roboto", sans-serif;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
.w3-wide {
    font-family: "Montserrat", sans-serif;
}

/* Custom styles for the sidebar */
.w3-sidebar a {
    font-weight: bold;
    text-decoration: none;
    display: block;
    padding: 16px;
}

.w3-sidebar a:hover {
    background-color: #ddd;
}

/* Custom styles for the top menu */
.w3-bar-item {
    padding: 16px;
    text-decoration: none;
    display: block;
}

/* Custom styles for the main content */
.w3-main {
    margin-left: 250px;
}

/* Custom styles for the footer */
.w3-black {
    color: white;
    text-align: center;
    padding: 24px;
}

/* Custom styles for the image header */
.w3-display-container {
    position: relative;
}

.w3-display-topleft,
.w3-display-topright {
    position: absolute;
    top: 0;
    padding: 24px 48px;
    color: white;
}

/* Additional custom styles can be added as needed */
